import * as actionTypes from '../../actions/actionsUser/actionTypes';
const initialState = {
    userInfo: {
        firstName: '',
        lastName: '',
        id: ''
    }
};


const reducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.SET_USER:
            return {
                ...state,
                userInfo: action.payload
            }
        default:
            return state;
    }
};

export default reducer;