import React, { useState, useRef, useEffect } from 'react'
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import Box from '@material-ui/core/Box';
import CloseIcon from '@material-ui/icons/Close';
import IconButton from '@material-ui/core/IconButton';
import styles from './RatingDialog.module.scss';
import Rating from '@material-ui/lab/Rating';
import Button from '@material-ui/core/Button'


const labels = {
    0.5: 'חסר תועלת',
    1: 'חסר תועלת+',
    1.5: 'ירוד',
    2: 'ירוד+',
    2.5: 'בסדר',
    3: 'בסדר+',
    3.5: 'טוב',
    4: 'טוב+',
    4.5: 'מצויין',
    5: 'מצויין+',
};

const RatingDialog = (props) => {
    const value = 0;
    const [isOpen, setOpen] = useState(false);
    const [hover, setHover] = React.useState(-1);

    const selectedRating = (ev, val) => {
        props.onSetRating(val)
    }

    const handleCloseDialog = () => {
        setOpen(false)
        props.setIsOpenEvent(false)
    }

    useEffect(() => {
        if (props.isOpen) {
            console.log('IS OPEN REU');
            setOpen(true)
        }
    }, [props.isOpen])

    return (
        <Dialog classes={{ root: styles.dialogContainer }} open={isOpen} onClose={handleCloseDialog} fullWidth={true}>
            <Box className={styles.topSection}>
                <IconButton classes={{ root: styles.iconBtnClose }} onClick={handleCloseDialog}>
                    <CloseIcon />
                </IconButton>
            </Box>
            <Box className={styles.containerContent}>
                <DialogTitle classes={{ root: styles.dialogTitle }} id="form-dialog-title">איך היית רוצה לדרג את הבייביסיטר</DialogTitle>
                <DialogContent classes={{ root: styles.dialogContentRate }}>
                    <Rating
                        classes={{ root: styles.rating }}
                        onChange={(event, value) => selectedRating(event, value)}
                        name="hover-side"
                        size="large"
                        value={value}
                        precision={0.5}
                        onChangeActive={(event, newHover) => {
                            setHover(newHover);
                        }}
                    />
                    <Box ml={2}>{labels[hover !== -1 ? hover : value]}</Box>
                </DialogContent>
                <DialogContent classes={{ root: styles.contentTextArea }}>
                    <textarea placeholder="ספר לנו על החוויה האישית שלך מהבייביסיטר הזה. האם זה היה שידוך טוב עבורך?"
                        className={styles.textAreaReview}></textarea>
                    <Button variant="contained" color="secondary">
                        שמירה
                </Button>
                </DialogContent>

            </Box>
        </Dialog>
    )
}

export default RatingDialog
